body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-color: rgb(36, 34, 34);
  color: bisque;
}

.btn--onoff {
  font-size: 32px;
  border-radius: 50%;
  width: 75px;
  height: 75px;
  background-color: rgb(36, 34, 34);
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}
.btn--red {
  color: red;
  border: 2px solid red;
}
.btn--green {
  color: green;
  border: 2px solid green;
}

.container {
  display: flex;
  align-items: center;
  flex-direction: column;
}
.container__manager {
  flex: 1 1;
  margin-top: 32px;
  background-color: rgba(255, 255, 255, 0.1);
  min-width: 320px;
}
.container__manager--title {
  text-align: center;
}
.container__manager--funds {
  text-align: center;
  font-size: larger;
}
.container__manager__onoff {
  display: flex;
  justify-content: center;
  margin-top: 32px;
}
.container__manager__logs {
  margin-top: 56px;
  border-top: 2px solid white;
  min-height: 160px;
  max-height: 240px;
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
}
.container__manager__logs__log {
  display: flex;
  justify-content: space-around;
  border-bottom: 1px solid whitesmoke;
}
.container__manager__logs__nothing {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: larger;
  flex: 1 1;
}

.auth__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.auth__container--title {
  text-align: center;
  margin-top: 56px;
}
.auth__container__form {
  display: flex;
  flex-direction: column;
  min-width: 240px;
  margin-top: 16px;
}
.auth__container__form--input {
  margin-top: 8px;
  height: 32px;
}
.auth__container__form--btn {
  margin-top: 8px;
  height: 32px;
}

